import React, { useEffect } from "react";

import { useDispatch, useSelector } from "react-redux";
import { withRouter } from "react-router";
import { TableEditable } from "Component/BootstrapTable";
import { ModalsCaptcha } from "Component/Modal/ModalConfirmation";
import "../index.scss";
import {
  showErrorsOnly,
  deleteErrorsField,
  setCsvData,
  submit,
  checking_error,
} from "./services.js";
import PopUpSavedCsv from "Component/Modal/PopUpSavedCsv";

const Upload = (props) => {
  const dispatch = useDispatch();

  //selector
  const selectorCsvFileName = useSelector((state) => state.csvFileName);
  const selectorCsvData = useSelector((state) => state.csvData);
  const selectorCsvField = useSelector((state) => state.csvField);
  const selectorWarehouseArea = useSelector((state) => state.warehouseAreaCsv);
  const selectorStatus = useSelector((state) => state.status); //wh
  const warehousesData = useSelector((state) => state.warehouseData);

  //state
  const [isShowModal, setIsShowModal] = React.useState(false);
  const [isHiddenRows, setIsHiddenRows] = React.useState(null);
  const [isShowErrorOnly, setIsShowErrorOnly] = React.useState(true);
  const [isValidate, setIsValidate] = React.useState(false);
  const [modalShow, setModalShow] = React.useState(false);
  const [checkingCsv, setCheckingCsv] = React.useState(true);
  const [submitStatus, setSubmitStatus] = React.useState(false);

  const [title, setTitle] = React.useState(null);
  const [msg1, setMsg1] = React.useState(null);
  const [msg2, setMsg2] = React.useState(null);
  const [cancelButtonTitle, setCancelButtonTitle] = React.useState(null);
  const [actionButtonTitle, setActionButtonTitle] = React.useState(null);
  const [isSubmitText, setIsSubmitText] = React.useState("Submit");
  const [cancelButtonClass, setCancelButtonClass] = React.useState(null);
  const [actionButtonClass, setActionButtonClass] = React.useState(null);
  const [colourTitle, setColourTitle] = React.useState(null);

  useEffect(() => {
    checking_error({
      csvData: selectorCsvData,
      csvField: selectorCsvField,
      setIsValidate,
      setCheckingCsv,
    });
  }, [checkingCsv]);

  // useEffect(() => {
  //   if (submitStatus === true) {
  //     props.history.push("/inventory-entry/stock-transfer/CSV-upload/result");
  //   }
  // }, [submitStatus]);

  useEffect(() => {
    if (submitStatus == "success") {
      setTitle("Success!");
      setMsg1(selectorCsvFileName + " upload successfull.");
      setCancelButtonTitle("Cancel");
      setActionButtonTitle("Select Again");
      setActionButtonClass("bg-green background");
      setColourTitle("green");
      setModalShow(true);
    } else if (submitStatus == "API") {
      setTitle("Failed!");
      setMsg1(selectorCsvFileName + " failed to upload.");
      setCancelButtonTitle("Cancel");
      setActionButtonTitle("Retry");
      setActionButtonClass("bg-red background");
      setColourTitle("red");
      setModalShow(true);
    } else if (submitStatus == "noData") {
      setTitle("Failed!");
      setMsg1(selectorCsvFileName + " failed to upload.");
      setCancelButtonTitle("Check"); 
      setCancelButtonClass("bg-red background");
      setActionButtonClass("d-none");
      setColourTitle("red");
      setModalShow(true);
    }
  }, [submitStatus]);

  if (selectorCsvData === undefined || selectorCsvData === null) {
    props.history.push("/inventory-entry/stock-transfer/CSV-upload");
    return 0;
  }

  const setCsvDataTmp = (param) => {
    setCsvData({ param, dispatch, setCheckingCsv });
    showErrorsOnly({
      csvData: selectorCsvData,
      csvField: selectorCsvField,
      isShowErrorOnly: false,
      setIsShowErrorOnly,
      setIsHiddenRows,
      setCheckingCsv,
    });
  };

  return (
    <div className="w-100 d-flex flex-column stock-take-summary">
      <TableEditable
        globalData={selectorCsvData} //Required
        columns={selectorCsvField} //Required
        tableClass="reviewTable"
        hiddenRows={isHiddenRows}
        warehouseArea={selectorWarehouseArea}
        setCsvDataTmp={(param) => setCsvDataTmp(param)}
        setIsValidate={(param) => setIsValidate(param)}
        dispatch={dispatch}
        setCheckingCsv={setCheckingCsv}
        warehousesData={warehousesData}
      />

      <div className="row pt-3">
        <div className="col-4">
          <button
            type="button"
            className="btn px-3 mr-3 text no-shadow"
            style={{ backgroundColor: "#fff" }}
            onClick={() =>
              showErrorsOnly({
                csvData: selectorCsvData,
                csvField: selectorCsvField,
                isShowErrorOnly,
                setIsShowErrorOnly,
                setIsHiddenRows,
                setCheckingCsv,
              })
            }
          >
            Show {isShowErrorOnly ? "Errors Only" : "All"}
          </button>

          <button
            type="button"
            className="btn px-3 mr-3  text no-shadow"
            style={{ backgroundColor: "#fff" }}
            onClick={() =>
              deleteErrorsField({
                csvData: selectorCsvData,
                csvField: selectorCsvField,
                dispatch,
                setCheckingCsv,
              })
            }
          >
            Delete Errors Field
          </button>
        </div>
        <div className="col-4" style={{ lineHeight: "40px" }}>
          Review your imported file: {selectorCsvFileName} (
          {selectorCsvData.length || 0} Lines)
        </div>
        <div className="col-4">
          <button
            type="button"
            className="btn px-5 bg-red text-white no-shadow float-right"
            onClick={() => props.history.goBack()}
          >
            Discard
          </button>

          <button
            type="button"
            class={
              "btn px-5 mr-3 text-white no-shadow float-right " +
              (isValidate === true && selectorCsvData?.length > 0 ? "bg-green" : "bg-primaryGrey disabled")
            }
            onClick={() => setIsShowModal(!isShowModal)}
          >
            Submit
          </button>
        </div>
      </div>

      <PopUpSavedCsv
        isSaved={submitStatus}
        status={selectorStatus}
        onCloseFunction={() => {
          setModalShow(false);
          setSubmitStatus(false);  
          if(submitStatus=='success'){  
            props.history.push("/inventory-entry/stock-transfer")
          }else if(submitStatus=='noData'){
            props.history.push("/inventory-entry/stock-transfer/CSV-upload/result");
          }
        }}
        statusDisplay={modalShow}
        title={title}
        cancelButtonTitle={cancelButtonTitle}
        actionButtonTitle={actionButtonTitle}
        colorText={colourTitle}
        refreshOnCancel={false}
        msg1={msg1}
        msg2={msg2}
        cancelButtonClass={cancelButtonClass}
        actionButtonClass={actionButtonClass + " ml-0"}
        actionButtonEvent={() =>
          {
            if(submitStatus=='success'){  
              props.history.push("/inventory-entry/stock-transfer/CSV-upload")
            } else if(submitStatus=='API'){
              props.history.push("/inventory-entry/stock-transfer/CSV-upload");
            }
        }
         
        }
      /> 
      <ModalsCaptcha
        submit={() =>
          submit({
            dispatch,
            csvData: selectorCsvData,
            csvField: selectorCsvField,
            setIsShowModal,
            setIsSubmitText,
            setSubmitStatus,
            setMsg1,
            setMsg2,
          })
        }
        submitText={isSubmitText}
        show={isShowModal}
        setIsShowModal={setIsShowModal}
      />
    </div>
  );
};

export default withRouter(Upload);
