import React from "react";
import {
  CButton,
  CCol,
  CModal,
  CModalBody,
  CModalFooter,
  CRow,
} from "@coreui/react";
import "Component/Modal/PopUp/index.scss";

const PopUpSavedCsv = ({
  isSaved,
  setIsSaved,
  confirmation,
  submit,
  statusDisplay = false,
  title = "",
  cancelButtonTitle = "Cancel",
  actionButtonTitle = "Submit",
  cancelButtonClass = "bg-background",
  actionButtonClass = "bg-background",
  actionButtonEvent = null,
  msg1 = null,
  msg2 = null,
  setShow,
  onCloseFunction,
}) => {
  const saved = {
    success: {
      headerIcon: "em em-tada",
      textColor: "green",
      button: "bg-green",
    },
    failed: {
      headerIcon: "em em-anguished",
      textColor: "red",
      button: "bg-red",
    },
  };
  const data = isSaved == "success" ? saved.success : saved.failed;

  const focus = document.getElementById("btn_is_saved");
  if (focus) focus.focus();
  return (
    <CRow className={statusDisplay ? null : "d-none"}>
      <CCol>
        <CModal
          show={statusDisplay}
          onClose={() => {
            onCloseFunction();
          }}
          size="lg"
          style={{ minHeight: "280px" }}
          bodyOpenClassName="p-3"
          closeOnBackdrop={false}
        >
          <div className="d-flex flex-row-reverse">
            <i
              className="pr-3 pt-3 iconU-modalClose"
              onClick={() => {
                onCloseFunction();
              }}
            ></i>
          </div>
          <CModalBody className="px-5 mt-3 py-0">
            <label className={`${data.textColor} LatoBold mr-2 h5 mb-3`}>
              {title}
            </label>{" "}
            <span>
              <i className={`${data.headerIcon}`}></i>
            </span>
            <div className="mr-1 h5 mb-3">{`${msg1}`}</div>
            <div
              style={{ backgroundColor: "#F5F7FA" }}
              className="w-100 border rounded px-3 pt-4 pb-3 mb-4"
            >
              {!Array.isArray(msg2) ? (
                <div className={`h5 font-weight-bold`}>{msg2}</div>
              ) : (
                msg2.map((datax, index) => {
                  return <div className={datax.className}> {datax.message} </div>;
                })
              )}
            </div>
          </CModalBody>
          <CModalFooter className="px-5 pb-5 pt-0 justify-content-start border-none">
            {cancelButtonTitle ? (
              <CButton
                className={`${cancelButtonClass} no-shadow px-5 mt-4 mb-0 mx-0`}
                onClick={() => {
                  onCloseFunction();
                }}
              >
                {cancelButtonTitle}
              </CButton>
            ) : null}
            {actionButtonTitle ? (
              <CButton
                className={`${actionButtonClass} text no-shadow px-5 mt-4 mb-0 ml-3 mr-0`}
                onClick={() => {
                  actionButtonEvent();
                }}
              >
                {actionButtonTitle}
              </CButton>
            ) : null}{" "}
          </CModalFooter>
        </CModal>
      </CCol>
    </CRow>
  );
};

export default PopUpSavedCsv;
